import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";

function App() {
  var [items, setItems] = React.useState([]);
  function createNote(note) {
    return (
      <Note
        key={note.index}
        title={note.title}
        content={note.content}
        onDelete={deleteItem}
        id={note.index}
      />
    );
  }
  function deleteItem(id) {
    setItems(prev => {
      return prev.filter((e, index) => {
        return e.index !== id;
      });
    });
  }
  function addNote(title, content, index) {
    var newNote = {
      title: title,
      content: content,
      index: index
    };
    setItems(prev => {
      return [...prev, newNote];
    });
  }
  return (
    <div>
      <Header />
      <CreateArea onClicked={addNote} />
      {items.map(createNote)}
      <Footer />
    </div>
  );
}

export default App;
